import { filter } from 'rxjs/operators';
import { element } from 'protractor';
import { AuthService } from './../auth.service';
import { Component, OnInit } from '@angular/core';
import * as _ from 'underscore';
import * as a from "lodash";
import { Router } from '@angular/router';
import { StoresService } from '../stores.service';
declare var $: any;
import { timer } from 'rxjs';


@Component({
  selector: 'app-online-store',
  templateUrl: './online-store.component.html',
  styleUrls: ['./online-store.component.styl']
})
export class OnlineStoreComponent implements OnInit {
  onlinestoredetails: any = [];
  spinner = false;
  coupentypedata: any = [];
  categories: any = [];
  CategoriesData: any;
  promocode: any;
  showCoupon = "login";
  public userid = sessionStorage.getItem("userid");
  showdeatils: any;
  coupentitle: any;
  coupendescription: any;
  coupenimages: any;
  storelink: any;
  storelinks: any;
  copycode: any;
  coupon_type: any;
  hidepopup: boolean = false;
  catageorytype: any;
  static_coupondata: any = [];
  store_names: any;
  // pager object
  pager: any = {};
  items = [];
  pageOfItems: Array<any>;
  filterType: any = null;
  pagedItems: any = [];


  constructor(
    private authService: AuthService,
    private storesService: StoresService,
    public router: Router,
  ) {
    this.filterType = this.router.getCurrentNavigation().extras.state ? this.router.getCurrentNavigation().extras.state.id : null;
    console.log(this.filterType, 'filterType');
    if (this.filterType) {
      this.getonlinestoredetails();
    }
  }

  ngOnInit() {
    const source = timer(5000);
    //output: 0
    const subscribe = source.subscribe(val => {
      this.getonlinestoredetails();
    });
    if (!this.filterType) {
      this.getonlinestoredetails();
    }
    // an example array of 150 items to be paged
    this.items = this.coupentypedata
    this.getcategories();
  }
  onChangePage(pageOfItems: Array<any>) {
    // update current page of items
    this.pageOfItems = pageOfItems;
  }

  goToLoginPage() {
    this.hidepopup = false
    $('#myModal').modal('hide');
    this.router.navigate(['/login']);
  }

  showInner() {
    $('#myModal').modal('hide');
    this.hidepopup = true
    this.router.navigate(['/store-inner'], { state: { id: this.showdeatils } });
  }

  // Get ONLINE STORE DETAILS
  getonlinestoredetails() {
    this.spinner = true;
    this.storesService.getonlinestoredata().subscribe((data) => {
      console.log("get online details", data);
      this.onlinestoredetails = data.data;
      this.static_coupondata = data.data
      console.log('online store data lost', this.onlinestoredetails);
      this.spinner = false;
      this.coupentypedata = _.uniq(this.onlinestoredetails, 'promo_id')
      console.log('top stotre data list', this.coupentypedata)
      this.categories = _.uniq(this.coupentypedata, 'category')
      console.log('category list', this.categories)

      if (this.filterType) {
        this.getcategorieslist(this.filterType);
      }
      // initialize to page 1
      this.setPage(1, 'first');
    });
  }


  //CATEGORIES LIST
  getcategories() {
    this.storesService.getCategories().subscribe(data => {
      console.log(data, 'categories list')
      if (data.success) {
        this.CategoriesData = data.data
        console.log('categores list data', this.CategoriesData);
      }
    })
  }

  openCoupon(value: any, item?: any) {
    console.log('itemdfadf', item)
    this.promocode = item.coupon_code
    this.showCoupon = value;
    this.hidepopup = true;
  }

  getcodedata(item: any) {
    this.hidepopup = true
    console.log('get code item value', item);
    this.showdeatils = item
    console.log('show details data', this.showdeatils);
    this.coupentitle = this.showdeatils.coupon_title ? this.showdeatils.coupon_title : null
    this.coupendescription = this.showdeatils.coupon_description ? this.showdeatils.coupon_description : null
    this.coupenimages = this.showdeatils.store_image ? this.showdeatils.store_image : null
    this.storelink = this.showdeatils.store_link
    this.promocode = this.showdeatils.coupon_code
    this.storelinks = this.showdeatils.store_link
    this.copycode = this.showdeatils.coupon_code
    this.coupon_type = this.showdeatils.coupon_type
    this.store_names = this.showdeatils.store_name
  }

  getcategorieslist(item) {
    console.log('item value list', item)
    this.catageorytype = this.filterType ? item.name : item.category;
    console.log('..............', this.catageorytype);
    this.pagedItems = _.filter(this.static_coupondata, (ele) => {
      return ele.category === this.catageorytype
    })
    console.log('coupon typwe data', this.pagedItems);
    this.setPage(1, 'pagination')
    console.log('coupon typwe data', this.coupentypedata);
  }

  setPage(page: number, val: any) {
    if (val === 'first') {
      if (page < 1 || page > this.pager.totalPages) {
        return;
      }
      console.log('all item data list', this.coupentypedata);
      // get pager object from service
      this.pager = this.storesService.getPager(this.coupentypedata.length, page);
      console.log('pager value list', this.pager)

      // get current page of items
      this.pagedItems = this.coupentypedata.slice(this.pager.startIndex, this.pager.endIndex + 1);

      console.log('total count pages list', this.pagedItems);
    }
    if (val === 'pagination') {
      if (page < 1 || page > this.pager.totalPages) {
        return;
      }
      console.log('all item data list', this.pagedItems);
      // get pager object from service
      this.pager = this.storesService.getPager(this.pagedItems.length, page);
      console.log('pager value list', this.pager)

      // get current page of items
      this.pagedItems = this.pagedItems.slice(this.pager.startIndex, this.pager.endIndex + 1);

      console.log('total count pages list', this.pagedItems);
    }
  }


}

