import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import * as _ from 'underscore';

@Injectable({
  providedIn: 'root'
})
export class StoresService {

  constructor(private http: HttpClient) { }

  getStoreList(body: object) {
    return this.http.post<any>(ApiService.API.GET_STORES_LIST, body);
  }

  getshopcategories() {
    return this.http.get<any>(ApiService.API.GET_SHOP_STORE_lIST);
  }

  getonlinestoredata() {
    return this.http.get<any>(ApiService.API.GET_ONLINE_SHOP_LIST);
  }

  getCategories() {
    return this.http.get<any>(ApiService.API.GET_ONLINE_CATEGORIES_LIST);
  }

  getInnerStoreService(obj: object) {
    return this.http.post<any>(ApiService.API.GET_INNER_STORE_DETAILS , obj)
  }

  storeUserCouponCode(obj: object) {
    return this.http.post<any>(ApiService.API.GET_INNER_STORE_COUPON , obj)
  }

  getPager(totalItems: number, currentPage: number = 1, pageSize: number = 10) {
    // calculate total pages
    let totalPages = Math.ceil(totalItems / pageSize);

    let startPage: number, endPage: number;
    if (totalPages <= 10) {
        // less than 10 total pages so show all
        startPage = 1;
        endPage = totalPages;
    } else {
        // more than 10 total pages so calculate start and end pages
        if (currentPage <= 6) {
            startPage = 1;
            endPage = 10;
        } else if (currentPage + 4 >= totalPages) {
            startPage = totalPages - 9;
            endPage = totalPages;
        } else {
            startPage = currentPage - 5;
            endPage = currentPage + 4;
        }
    }

    // calculate start and end item indexes
    let startIndex = (currentPage - 1) * pageSize;
    let endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    let pages = _.range(startPage, endPage + 1);

    // return object with all pager properties required by the view
    return {
        totalItems: totalItems,
        currentPage: currentPage,
        pageSize: pageSize,
        totalPages: totalPages,
        startPage: startPage,
        endPage: endPage,
        startIndex: startIndex,
        endIndex: endIndex,
        pages: pages
    };
}
}
