import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import {environment} from '../environments/environment'

@Injectable({
  providedIn: 'root'
})
export class EncryptionService {

  constructor() { }

  encrypt(text :string) {
    return CryptoJS.AES.encrypt(text, environment.SECURITY_KEY).toString();
  }

  decrypt(textToDecrypt : string){
    return CryptoJS.AES.decrypt(textToDecrypt, environment.SECURITY_KEY).toString(CryptoJS.enc.Utf8);
  }
}
