import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, NavigationStart, Router, RouterState, RouterStateSnapshot } from '@angular/router';
import { AuthService } from '../auth.service';
import { map, mapTo, filter, switchMap, tap } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { StoresService } from '../stores.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import * as _ from "underscore";
import swal from 'sweetalert';
declare var $: any;

@Component({
  selector: "app-store-inner",
  templateUrl: "./store-inner.component.html",
  styleUrls: ["./store-inner.component.styl"],
})
export class StoreInnerComponent implements OnInit {
  innerStoreDetails: any = [];
  spinner = false;
  StoreItem: any = 0;
  randomCode: any;
  currentItem:any;
  spinner1 = false;
  offlineCards: any = [];
  showprofile = true;
  couponShow = false;
  public userid = sessionStorage.getItem("userid");
  public email = sessionStorage.getItem('email');
  public points = sessionStorage.getItem('points');
  public username = sessionStorage.getItem('username');
  CarouselOptions = {
    items: 4,
    dots: false,
    nav: true,
    navText: [
      "<i class='fas fa-chevron-left'></i>",
      "<i class='fas fa-chevron-right'></i>",
    ],
  };
  constructor(
    public authService: AuthService,
    private storesService: StoresService,
    private route: ActivatedRoute, 
    private router: Router,
    private _toastr: ToastrManager
  ) {
    this.StoreItem =
      this.router.getCurrentNavigation() &&
      this.router.getCurrentNavigation().extras &&
      this.router.getCurrentNavigation().extras.state
        ? this.router.getCurrentNavigation().extras.state.id
        : {};
    console.log(this.StoreItem, "StoreItem");
   }

  ngOnInit() {
    this.StoreItem && this.StoreItem.id ? sessionStorage.setItem("StoreId", this.StoreItem.id) : 0;
    this.getInnerStoreDetials();
    console.log(this.StoreItem, "id");
    const body = {
      limit: 12,
      offset: 0,
    };
    this.getStoresList(body);
    // $(function () {
    //   $('#owl-single-product').owlCarousel({
    //     items: 1,
    //     itemsTablet: [768, 3],
    //     itemsDesktop: [1199, 1],
    //     itemsDesktopSmall: [768, 3]

    //   });

    //   $('#owl-single-product-thumbnails').owlCarousel({
    //     items: 4,
    //     pagination: true,
    //     rewindNav: true,
    //     itemsTablet: [992, 4],
    //     itemsDesktopSmall: [768, 4],
    //     itemsDesktop: [992, 1]
    //   });

    //   $('#owl-single-product2-thumbnails').owlCarousel({
    //     items: 6,
    //     pagination: true,
    //     rewindNav: true,
    //     itemsTablet: [768, 4],
    //     itemsDesktop: [1199, 3]
    //   });

    //   $('.single-product-slider').owlCarousel({
    //     stopOnHover: true,
    //     rewindNav: true,
    //     singleItem: true,
    //     pagination: true
    //   });

    // });
  }

  copyCouponHere() {
    this.couponShow = false;
    this.showprofile = false;
    const cop = {
      code: this.randomCode,
      userid: this.email,
      points: +this.points - 1,
      coupons_id:this.currentItem.id,
      username : this.username
    };
    this.storesService.storeUserCouponCode(cop).subscribe(
      (data: any) => {
      console.log(data, "data");
      if (data && data.success) {
        this._toastr.successToastr(data.message);
        sessionStorage.setItem("points", data.points);
        this.getInnerStoreDetials();
        this.couponShow = true;
      } else {
        this._toastr.errorToastr(data.message);
      }
      this.showprofile = true;
    },
    (err:any)  => {
      if (err && !err.error.success) {
        $('#myModal').modal('hide');
        this._toastr.errorToastr(err.error.message);
      }
    }
    );
  }

  getCouponCode(item:any) {
    this.randomCode = ''
    // console.log(item, 'current item')
    // if (!this.authService.isLoggedIn()) {
    //   this._toastr.errorToastr(`Login Please...`);
    //   $('#myModal').modal('hide');
    //   return;
    // } else {
      $('#myModal').modal('show');
      this.currentItem = item;
      this.randomCode = Math.random().toString(36).substr(2, 5).toUpperCase();
  // }
}

  getInnerStoreDetials() {
    this.spinner = true;
    const obj = { id: sessionStorage.getItem('StoreId') ? +sessionStorage.getItem('StoreId') : 0, email: this.email  };
    this.storesService.getInnerStoreService(obj).subscribe((data: any) => {
      console.log(data, "response for coupons");
      if (data && data.success) {
        this.innerStoreDetails = data.data;
        if (this.innerStoreDetails && this.innerStoreDetails.length && this.currentItem && this.currentItem.id) {
          const Current = _.filter(this.innerStoreDetails, (o:any) => {
            return +o.id === +this.currentItem.id
          })
          Current && Current.length ? this.currentItem = Current[0] : null;
        }
      }
      this.spinner = false;
    });
  }

  // stores list
  getStoresList(body: object) {
    this.spinner1 = true;
    console.log(body, "body");
    this.storesService.getStoreList(body).subscribe((data) => {
      console.log(data, "resp");
      this.spinner1 = false;
      if (data.success) {
        this.offlineCards = data.data;
        _.map(this.offlineCards, (o: any) => {
          o.img = "assets/images/no.jpg";
        });
      }
    });
  }

  goToLoginPage() {
    this.router.navigate(["/login"]);
  }

  callPop() {
    this.randomCode = Math.random().toString(36).substr(2, 5).toUpperCase();
    swal({
      title: "Are you sure?",
      text: "Once confirmed, you will not be retain the amount",
      icon: "warning",
      buttons: [true, "Confirm"],
      dangerMode: true,
    })
    .then((willDelete) => {
      if (willDelete) {
        swal("Great !! You have purchased the coupon", {
          icon: "success",
        });
        this.copyCouponHere();                      
      } else {
        swal("Your amount is safe!");
      }
    });

  }



}
