import { Injectable } from '@angular/core';
import { ActivatedRoute, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { AuthService } from './auth.service';
import { Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class AuthGuardService {

  userId = sessionStorage.getItem('id');
  role = sessionStorage.getItem('role');

  constructor(
    private authService: AuthService,
    private route: Router,
    private _route: ActivatedRoute
  ) { }

  canActivate(state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (!this.authService.isLoggedIn()) {
      this.route.navigate(['']);
      return false;
    } else {
      // return true;
      if (this.role == 'admin' || this.role == 'user' || this.role == 'manager') {
        this.route.navigate(['/login']);
        return true;
      } else {
        this.route.navigate([''], { queryParams: { returnUrl: state.url } });
        return false;
      }
    }
  }
 

}
