import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import * as _ from 'underscore';
@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private http: HttpClient) { }

  userLogin(data: any) {
    return this.http.post<any>(ApiService.API.USER_LOGIN, data);
  }

  userSignup(data: any) {
    return this.http.post<any>(ApiService.API.USER_SIGNUP, data);
  }

  getUserToken() {
    return localStorage.getItem('token');
  }

  getUserId() {
    return Number(sessionStorage.getItem('userid'));
  }

  getUserRole() {
    return sessionStorage.getItem('role');
  }

  getUserPayload() {
    var token = this.getUserToken();
    if (token) {
      var userPayload = atob(token.split('.')[1]);
      return JSON.parse(userPayload);
    }
    else
      return null;
  }

  isLoggedIn(): boolean {
    var userPayload = this.getUserPayload();
    if (userPayload)
      return userPayload.exp > Date.now() / 1000;
    else
      return false;
  }

  signInWithGoogleFb(data: any) {
    return this.http.post<any>(ApiService.API.USER_SIGNIN_GOOGLE_FB, data);
  }

  sendRequestResetPassword(data: any) {
    return this.http.post<any>(ApiService.API.FORGOT_PASSWORD, data);
  }

  sendOtpEmailVerification(data: any) {
    return this.http.post<any>(ApiService.API.ADD_OTP_VERIFICATION, data);
  }

  updateProfileInfo(data: any) {
    return this.http.post<any>(ApiService.API.UPDATE_USER_PROFILE_INFORMATION, data);
  }

  updatePasswordInfo(data: any) {
    return this.http.post<any>(ApiService.API.UPDATE_USER_PASSWORD_INFORMATION, data);
  }

  userLogout(data: any) {
    return this.http.post<any>(ApiService.API.USER_LOGOUT, data);
  }

  getStoreList(body: object) {
    return this.http.post<any>(ApiService.API.GET_STORES_LIST, body);
  }

  getshopcategories() {
    return this.http.get<any>(ApiService.API.GET_SHOP_STORE_lIST);
  }

  getonlinestoredata() {
    return this.http.get<any>(ApiService.API.GET_ONLINE_SHOP_LIST);
  }

  getCategories(){
    return this.http.get<any>(ApiService.API.GET_ONLINE_CATEGORIES_LIST);
  }

  getInnerStoreService(obj:object) {
    return this.http.post<any>(ApiService.API.GET_INNER_STORE_DETAILS , obj)
  }

  storeUserCouponCode(obj:object) {
    return this.http.post<any>(ApiService.API.GET_INNER_STORE_COUPON , obj)
  }


}
