import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { ToastrManager } from "ng6-toastr-notifications";
import { AuthService } from "src/app/auth.service";
import { AdminService } from "../admin.service";
// import { timer } from 'rxjs';
declare var $: any;

@Component({
  selector: "app-admin-page",
  templateUrl: "./admin-page.component.html",
  styleUrls: ["./admin-page.component.styl"],
})
export class AdminPageComponent implements OnInit {

  coupon: any = '';

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public authService: AuthService,
    private adminService: AdminService,
    public toastr: ToastrManager
  ) {}

  ngOnInit() {
    $(document).ready(function () {
      $("li.nav-item").click(function (e) {
        e.preventDefault();
        $(".nav-item").removeClass("active");
        $(this).addClass("active");
      });
    });

    // const source = timer(1000, 2000);
    // const subscribe = source.subscribe(val => console.log(val));
  }

  checkValidity() {
    if (!this.coupon || this.coupon == '') {
      return this.toastr.errorToastr('Please enter valid coupon.');
    }

    this.adminService.checkCouponValidity({coupon: this.coupon}).subscribe((response: any) => {
      console.log("Get coupon validity response", response);
      if (response.success) {
        this.toastr.successToastr(response.message);
      } else {
        this.toastr.errorToastr(response.message);
      }
    });
  }

}
