import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { AuthService } from '../auth.service';
import { SocialAuthService } from 'angularx-social-login';
import { SocialUser } from 'angularx-social-login';
import { ToastrManager } from 'ng6-toastr-notifications';
import * as moment from 'moment';
declare var $: any;
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.styl']
})
export class HeaderComponent implements OnInit {

  pageType: any;  
  userData: any = [];
  public userid = sessionStorage.getItem('userid');
  public username = sessionStorage.getItem('username');
  public points = sessionStorage.getItem('points');
  public imgUrl = sessionStorage.getItem('imgUrl');
  public provider = sessionStorage.getItem('provider');
  subscription: Subscription;
  notificationsCount: any = 0;
  currentUrl: any = '';
  User: SocialUser;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public authService: AuthService,
    private _socialAuthService: SocialAuthService,
    public toastr: ToastrManager
  ) { }

  ngOnInit() {
 
    $(document).ready(function(){
      $('.ScrollTop').click(function() {
        $('html, body').animate({scrollTop: 0}, 800);
      return false;
        });
    });


    this.pageType = this.route.snapshot.routeConfig.path;
    console.log('current page type issss ==>', this.pageType);  

    //****** TO CHECK WHEATHER THE USER IS SIGN IN WITH EMAIL OR NOT */
    this._socialAuthService.authState.subscribe((user: any) => {
      console.log("Response of user details",user);
      this.User = user;
    });
  }

  signOut() {
    const body: any = {
      id: this.userid,
      is_active: 0,
      remember_token: null,
      logout_time: moment().format('YYYY-MM-DD HH:mm:ss')
    }
    console.log('Post payload for user logout', body);

    this.authService.userLogout(body).subscribe((response: any) => {
      console.log("Get user logout response", response);
      if (response.success) {
        localStorage.clear();
        sessionStorage.clear();
        this.router.navigate(['/login']);
        if (this.provider !== 'BACATA') {
          this._socialAuthService.signOut();
        }
      } else {
        this.toastr.errorToastr(response.message);
      }
    })
  }

  getUserProfile() {
    this.username = sessionStorage.getItem('username');
  }

}
