import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SocialAuthService, SocialUser } from 'angularx-social-login';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Subscription } from 'rxjs';
import { AuthService } from '../auth.service';
import { HeaderComponent } from '../header/header.component';
import * as moment from 'moment';
import { NgModel } from '@angular/forms';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.styl']
})
export class ProfileComponent implements OnInit {

  pageType: any;  
  userData: any = [];
  public userid = sessionStorage.getItem('userid');
  public email = sessionStorage.getItem('email');
  public username = sessionStorage.getItem('username');
  public fullname = sessionStorage.getItem('username');
  public phoneNumber = sessionStorage.getItem('phonenumber');
  public points = sessionStorage.getItem('points');
  public imgUrl = sessionStorage.getItem('imgUrl');
  public provider = sessionStorage.getItem('provider');
  public updated_date = sessionStorage.getItem('updated_at');
  subscription: Subscription;
  notificationsCount: any = 0;
  currentUrl: any = '';
  User: SocialUser;
  @ViewChild(HeaderComponent, {static: true}) _headerComponentRef: HeaderComponent;
  currentPassword: any = null;
  newPassword: any = null;
  confirmPassword: any = null;
  @ViewChild('userName', {static: false}) userNameRef: NgModel;
  @ViewChild('currentPass', {static: false}) currentPassRef: NgModel;
  @ViewChild('newPass', {static: false}) newPassRef: NgModel;
  @ViewChild('confirmPass', {static: false}) confirmPassRef: NgModel;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public authService: AuthService,
    private _socialAuthService: SocialAuthService,
    public toastr: ToastrManager
  ) { }

  ngOnInit() {
    this.pageType = this.route.snapshot.routeConfig.path;
    console.log('current page type issss ==>', this.pageType);  

    //****** TO CHECK WHEATHER THE USER IS SIGN IN WITH EMAIL OR NOT */
    this._socialAuthService.authState.subscribe((user: any) => {
      console.log("Response of user details",user);
      this.User = user;
    });

    this.getUserProfile();

    this._headerComponentRef.getUserProfile();
  }

  getUserProfile() {
    this.username = sessionStorage.getItem('username');
    this.fullname = sessionStorage.getItem('username');
    this._headerComponentRef.getUserProfile();
  }

  updateProfile() {
    const profilePayload: any = {
      user_id: Number(this.userid),
      username: this.username,
    }
    console.log("Post payload for update profile", profilePayload);

    this.authService.updateProfileInfo(profilePayload).subscribe((response: any) => {
      console.log("Get response of update profile", response);
      if (response.success) {
        this.toastr.successToastr(response.message);
        sessionStorage.setItem('username', this.username);
        sessionStorage.setItem('created_at', moment().format('YYYY-MM-DD HH:mm:ss'));
        sessionStorage.setItem('updated_at', moment().format('YYYY-MM-DD HH:mm:ss'));
        this.getUserProfile();
        // this.userNameRef.reset();
        // this.username = this.username;
      } else {
        this.toastr.errorToastr(response.message);
      }
    });
  }

  updatePassword() {
    if (this.newPassword !== this.confirmPassword) {
      this.toastr.errorToastr('Passwords not match.');
      return;
    }

    const passwordPayload: any = {
      user_id: Number(this.userid),
      current_password: this.currentPassword,
      new_password: this.newPassword
    }
    console.log("Post payload for update profile", passwordPayload);

    this.authService.updatePasswordInfo(passwordPayload).subscribe((response: any) => {
      console.log("Get response of update profile", response);
      if (response.success) {
        this.toastr.successToastr(response.message);
        sessionStorage.setItem('created_at', moment().format('YYYY-MM-DD HH:mm:ss'));
        sessionStorage.setItem('updated_at', moment().format('YYYY-MM-DD HH:mm:ss'));
        this.getUserProfile();
        this.currentPassRef.reset();
        this.newPassRef.reset();
        this.confirmPassRef.reset();
        this.currentPassword = null;
        this.newPassword = null;
        this.confirmPassword = null;
      } else {
        this.toastr.errorToastr(response.message);
      }
    });
  }

}
