import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from '../api.service';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(private http: HttpClient) { }

  adminLogin(data: any) {
    return this.http.post<any>(ApiService.API.ADMIN_LOGIN, data);
  }

  checkCouponValidity(data: any) {
    return this.http.post<any>(ApiService.API.COUPON_VALIDITY_CHECK, data);
  }
}
