import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';
import { StoresService } from '../stores.service';
declare var $: any;


@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.styl']
})
export class CategoriesComponent implements OnInit {
  StoreData: any = [];
  pageNumber: any = []
  CategoriesData: any = [];
  constructor(
    public _authService: AuthService,
    private storesService: StoresService,
    private router: Router
  ) { }

  ngOnInit() {
    this.getcategories();
    const body = {
      limit : 9,
      offset : 0
    }
    // for page number show 
    this.pageNumber.push(1)
    this.getStoresList(body);
  }

  pageNavigate(pageNumber: string) {
    let body = {}
    if (pageNumber) {
      body = {
        limit: 10,
        offset: +(`${+pageNumber - 1}` + `0`)
      }
      this.getStoresList(body);
    }
  }
  // stores list 
  getStoresList(body: object) {
    console.log(body, 'body')
    this.storesService.getStoreList(body).subscribe(data => {
      console.log(data, 'resp')
      if (data.success) {
        this.StoreData = data.data
      }
    })
  }

  //CATEGORIES LIST
  getcategories() {
    this.storesService.getCategories().subscribe(data => {
      console.log(data, 'categories list')
      if (data.success) {
        this.CategoriesData = data.data
        console.log('categores list data', this.CategoriesData);
      }
    })
  }
  
  showInner(item:any) {
    this.router.navigate(['/store-inner'], { state: { id: item } });
  }
}
