          sessionStorage.setItem('username', user.name);
          sessionStorage.setItem('email', user.email);
          sessionStorage.setItem('password', '1234');
          sessionStorage.setItem('phonenumber', '9876543210');
          sessionStorage.setItem('role', 'user');
