          sessionStorage.setItem('points', response['data'][0]['points']);
          sessionStorage.setItem('provider', user.provider);
          sessionStorage.setItem('imgUrl', user.photoUrl);
          sessionStorage.setItem('facebook_id', null);
          sessionStorage.setItem('google_id', user.id);
          sessionStorage.setItem('dob', null);
          sessionStorage.setItem('address', null);
          sessionStorage.setItem('website', 'www.bacata.com');
          sessionStorage.setItem('brief', null);
          sessionStorage.setItem('is_admin', response['data'][0]['is_admin']);
          sessionStorage.setItem('is_active', response['data'][0]['is_active']);
          sessionStorage.setItem('confirmed', response['data'][0]['confirmed']);
          sessionStorage.setItem('confirmation_code', response['data'][0]['confirmation_code']);
          sessionStorage.setItem('login_time', moment().format('YYYY-MM-DD HH:mm:ss'));
          sessionStorage.setItem('logout_time', null);
