import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuardService } from './auth-guard.service';
import { BecomePartnerComponent } from './become-partner/become-partner.component';
import { CategoriesComponent } from './categories/categories.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { ProfileComponent } from './profile/profile.component';
import { StoreInnerComponent } from './store-inner/store-inner.component';
import { OnlineStoreComponent } from './online-store/online-store.component';
import { AdminLoginComponent } from './access/admin-login/admin-login.component';
import { AdminPageComponent } from './access/admin-page/admin-page.component';
import { FaqComponent } from './faq/faq.component';
import { InviteUsComponent } from './invite-us/invite-us.component';



const routes: Routes = [

  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full'
  },
  {
    path: 'dashboard',
    component: DashboardComponent
  },
  {
    path: 'categories',
    // canActivate: [AuthGuardService],
    component: CategoriesComponent
  },
  {
    path: 'store-inner',
    // canActivate: [AuthGuardService],
    component: StoreInnerComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  
  {
    path: 'profile',
    component:  ProfileComponent
  },
  {
    path: 'become-partner',
    component:  BecomePartnerComponent
  },
  {
    path: 'contact-us',
    component:  ContactUsComponent
  },
  {
    path:'onlinestore',
    component:OnlineStoreComponent
  },
  {
    path: 'admin-login',
    component: AdminLoginComponent
  },
  {
    path: 'admin-page',
    component: AdminPageComponent
  },
  {
    path: 'faq-page',
    component: FaqComponent
  },
  {
    path: 'invite-us',
    component: InviteUsComponent
  },

  { path: '**', component: DashboardComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
