import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SocialAuthService, SocialUser } from 'angularx-social-login';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/auth.service';
import { StoresService } from 'src/app/stores.service';
import { AdminService } from '../admin.service';
import * as moment from 'moment';
import * as _ from 'underscore';
@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.styl']
})
export class AdminLoginComponent implements OnInit {

  pageType: any;  
  userData: any = [];
  public userid = sessionStorage.getItem('userid');
  public username = sessionStorage.getItem('username');
  public points = sessionStorage.getItem('points');
  public imgUrl = sessionStorage.getItem('imgUrl');
  public provider = sessionStorage.getItem('provider');
  subscription: Subscription;
  notificationsCount: any = 0;
  currentUrl: any = '';
  User: SocialUser;

  email: any = null;
  password: any = null;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public authService: AuthService,
    private adminService: AdminService,
    private storesService: StoresService,
    private _socialAuthService: SocialAuthService,
    public toastr: ToastrManager
  ) { }

  ngOnInit() {
    this.pageType = this.route.snapshot.routeConfig.path;
    console.log('current page type issss ==>', this.pageType);  

    //****** TO CHECK WHEATHER THE USER IS SIGN IN WITH EMAIL OR NOT */
    this._socialAuthService.authState.subscribe((user: any) => {
      console.log("Response of user details",user);
      this.User = user;
    });
  }

  submitLogin() {
    const adminPayload: any = {
      email: this.email,
      password: this.password
    }
    console.log("Post payload to login as admin", adminPayload);

    this.adminService.adminLogin(adminPayload).subscribe((response: any) => {
      console.log("Get admin login response isss", response);
      if (response.success) {
        localStorage.setItem('token', response['data'][0]['remember_token']);
        sessionStorage.setItem('userid', response['data'][0]['id']);
        sessionStorage.setItem('username', response['data'][0]['name']);
        sessionStorage.setItem('email', response['data'][0]['email']);
        sessionStorage.setItem('password', response['data'][0]['password']);
        sessionStorage.setItem('phonenumber', response['data'][0]['mobile']);
        sessionStorage.setItem('role', response['data'][0]['role']);
        sessionStorage.setItem('status', response['data'][0]['status']);
        sessionStorage.setItem('points', response['data'][0]['points']);
        sessionStorage.setItem('provider', response['data'][0]['provider']);
        sessionStorage.setItem('imgUrl', response['data'][0]['image']);
        sessionStorage.setItem('facebook_id', response['data'][0]['facebook_id']);
        sessionStorage.setItem('google_id', response['data'][0]['google_id']);
        sessionStorage.setItem('dob', response['data'][0]['dob']);
        sessionStorage.setItem('address', response['data'][0]['address']);
        sessionStorage.setItem('website', response['data'][0]['website']);
        sessionStorage.setItem('brief', response['data'][0]['brief']);
        sessionStorage.setItem('is_admin', response['data'][0]['is_admin']);
        sessionStorage.setItem('is_active', response['data'][0]['is_active']);
        sessionStorage.setItem('confirmed', response['data'][0]['confirmed']);
        sessionStorage.setItem('confirmation_code', response['data'][0]['confirmation_code']);
        sessionStorage.setItem('login_time', response['data'][0]['login_time']);
        sessionStorage.setItem('logout_time', response['data'][0]['logout_time']);
        sessionStorage.setItem('created_at', moment(response['data'][0]['created_at']).format('YYYY-MM-DD HH:mm:ss'));
        sessionStorage.setItem('updated_at', moment(response['data'][0]['updated_at']).format('YYYY-MM-DD HH:mm:ss'));
        this.toastr.successToastr(response.message);
        this.router.navigate(['/admin-page']);
      } else {
        this.toastr.errorToastr(response.message);
      }
    });
  }

}
